@import (reference) 'common/color.less';
.sign-up-box{margin:0 auto;margin-top:50px;}
.sign-up-content {
  position: relative;
  width: 457px;
  min-height:595px;
  border: 1px dashed @dashed;
  margin: 20px auto;
  padding-left:97px;
  padding-top:34px;
  padding-bottom:34px;
  .triangle-bg {
    width: 59px;
    height: 54px;
    position: absolute;
    top: -1px;
    right: -1px;
    background: url('../../bundle/img/triangle.png') center center no-repeat;
  }
}
.input{width:160px;height:25px;display:inline-block;}
.select{width:77px;height:27px;display:inline-block}
.mr15{margin-right:15px;}
.mb22{margin-bottom:22px;}
.class-time{width:95px;height:25px;display:inline-block}
.class-time-news{width:235px;height:49px;border:1px solid #bababa;overflow-y:auto;padding:8px 20px;}
.class-time-news input{vertical-align:middle}
.next{margin-left:125px;margin-top:36px;}
//错误信息提示框
.alert-error{display:inline-block;border:1px solid @red;border-radius:4px;color:@red;padding:2px 10px;}
//模态框样式开始
.student-messa .popup{
  width:488px;
  height:270px;
  margin-left:-244px;
  margin-top:-135px;
}
.student-messa   .popupbox {
  width: 488px;
  height: 220px;
}
.left-news{
  margin-top:30px;
  margin-left:30px;
  width:144px;
  height:95px;
  background:url("../../bundle/img/double-bg.png") no-repeat;
  background-size:100% 100%;
  padding:10px 20px;
}
.arrow{
  width:36px;
  height:30px;
  display:inline-block;
  background: url("../../bundle/img/icon.png") no-repeat;
  background-position:-202px -308px;
  margin-left:14px;
  margin-top:80px;
}
.right-news{
  width:183px;
  height:63px;
  background:url("../../bundle/img/double-bg.png") no-repeat;
  background-size:100% 100%;
  margin-left:14px;
  margin-top:53px;
  text-align:center;
  line-height:63px;
}
//成功弹出框开始
.success-popup .pop-up{
  width:280px;
  height:350px;
  margin-left:-140px;
  margin-top:-175px;
  overflow:hidden;
}
.right-icon{
  width:85px;
  height:85px;
  display:inline-block;
  background:url("../../bundle/img/icon.png") no-repeat;
  background-position:-495px -260px;
}
.mt13{margin-top:13px;}
//成功弹出框结束
